import { ProfileAccountType } from 'types/__generated__/graphql';

import ROUTES from 'constants/Routes';

interface VerifiedTooltipProps {
  accountType: ProfileAccountType;
}

export default function VerifiedTooltip({ accountType }: VerifiedTooltipProps) {
  if (
    accountType !== ProfileAccountType.Collection &&
    accountType !== ProfileAccountType.Creator
  )
    return null;
  return (
    <div>
      This&nbsp;
      {accountType === ProfileAccountType.Collection ? 'collection' : 'creator'}
      &nbsp; has been verified.
      <br />
      <a
        className="invisibleAnchor"
        href={`${ROUTES.FAQ.THIRD_PARTY_AUTHENTIC()}`}
        target="_blank"
        rel="noreferrer"
      >
        Click to learn more&nbsp;&gt;
      </a>
    </div>
  );
}
