import {
  ChangeEvent,
  FocusEvent,
  useCallback,
  useEffect,
  useMemo,
  useRef,
  useState,
} from 'react';
import { Formik, validateYupSchema, yupToFormErrors } from 'formik';
import { noop } from 'lodash';
import { useMutation } from 'react-relay';
import { useNavigate } from 'react-router-dom';
import { UploadableMap } from 'relay-runtime';
import * as Yup from 'yup';

import {
  MPActionButton,
  MPColorClass,
  MPColorValue,
  MPDialog,
  MPFonts,
  MPStyledTextField,
  MPTooltip,
  useIsMobile,
} from '@mp-frontend/core-components';
import { InfoIcon, NotifySaleIcon } from '@mp-frontend/core-components/icons';
import { charCount, joinClasses } from '@mp-frontend/core-utils';

import EditProfile, {
  ProfilesMutation,
} from 'graphql/__generated__/ProfilesMutation.graphql';
import {
  MpErrors,
  ProfileInput,
  StoreLinkLinkType,
} from 'types/__generated__/graphql';

import {
  DISCORD_PROTOCOLLESS_USR_BASE_URL,
  INSTAGRAM_PROTOCOLLESS_BASE_URL,
  TWITTER_PROTOCOLLESS_BASE_URL,
} from 'constants/ExternalUrls';
import { ProfileImg } from 'pages/profiles/banner';
import useProfileData, {
  useProfileDataQueryRef,
} from 'pages/profiles/useProfileData';
import CSSIn from 'types/enums/css';
import CSSGap from 'types/enums/css/Gap';
import CSSGlobal from 'types/enums/css/Global';
import useOnEnterKey from 'utils/eventHandlers/useOnEnterKey';
import sanitizeUrl from 'utils/sanitizeUrl';

import * as inputStyles from 'css/global/Inputs.module.css';
import * as bannerStyles from 'css/pages/profiles/banner/Banner.module.css';
import * as styles from 'css/pages/profiles/dialog/EditProfile/index.module.css';

const TextFieldSx = {
  margin: '10px 0',
  width: '100%',
};

interface SocialMediaTextFieldProps {
  fieldValue: string;
  handleChange: (e: ChangeEvent<any>) => void;
  onBlur: (e: FocusEvent<any>) => void;
  setFieldValue: (a: string, v: string) => void;
  value: string;
  placeholder?: string;
  prefix?: string;
}

function SocialMediaTextField({
  fieldValue,
  setFieldValue,
  handleChange,
  onBlur,
  value,
  placeholder,
  prefix = '',
}: SocialMediaTextFieldProps) {
  const setValue = useCallback(
    (v) => setFieldValue(fieldValue, v),
    [fieldValue, setFieldValue]
  );

  const startAdornment = prefix ? (
    <span className={joinClasses(styles.socialMediaPrefix, MPFonts.inputText)}>
      {prefix}
    </span>
  ) : undefined;

  return (
    <MPStyledTextField
      textFieldClassName={styles.socialMediaTextField}
      placeholder={placeholder}
      sx={TextFieldSx}
      startAdornment={startAdornment}
      name={fieldValue}
      setValue={setValue}
      onChange={handleChange}
      onBlur={onBlur}
      endAdornment={false}
      value={value}
    />
  );
}

interface EditProfileDialogProps {
  isOpen: boolean;
  profileQueryRef: useProfileDataQueryRef;
  setOpen: (open: boolean) => void;
}

const defaultFormikProps = {
  handleSubmit: noop,
  values: [],
};

interface EditProfileDialogFormElement extends HTMLFormElement {
  elements: HTMLFormControlsCollection & {
    aboutBanner: HTMLInputElement;
    profileImage: HTMLInputElement;
  };
}

const DraftSchema = Yup.object().shape({
  shortDescription: Yup.string().test(
    'less-than-170 chars',
    'Bio is too long (maximum 170 characters)',
    (val) => charCount(val || '') <= 170
  ),
});

function validateForm(values) {
  try {
    validateYupSchema(values, DraftSchema, true);
  } catch (err) {
    return yupToFormErrors(err);
  }
  return undefined;
}

export default function EditProfileDialog({
  isOpen,
  setOpen,
  profileQueryRef,
}: EditProfileDialogProps) {
  const profileData = useProfileData(profileQueryRef);
  const navigate = useNavigate();

  const cancel = useCallback(() => {
    setOpen(false);
  }, [setOpen]);

  const form = useRef<EditProfileDialogFormElement>(null);
  const [currentBanner, setCurrentBanner] = useState(null);
  const [currentProfileImage, setCurrentProfileImage] = useState(null);

  const bannerImage = useMemo(
    () =>
      currentBanner
        ? URL.createObjectURL(currentBanner)
        : profileData.aboutBanner ?? undefined,
    [currentBanner, profileData.aboutBanner]
  );
  const profileImage = useMemo(
    () =>
      currentProfileImage
        ? URL.createObjectURL(currentProfileImage)
        : profileData.profileImageUrl ?? '/static/img/default-profile.png',
    [currentProfileImage, profileData.profileImageUrl]
  );

  const bannerInputRef = useRef<HTMLInputElement | null>(null);
  const openBannerPicker = useCallback(() => {
    if (bannerInputRef.current) bannerInputRef.current.click();
  }, []);
  const ariaOpenBannerPicker = useOnEnterKey(openBannerPicker);

  const profileInputRef = useRef<HTMLInputElement | null>(null);
  const openProfileImagePicker = useCallback(() => {
    if (profileInputRef.current) profileInputRef.current.click();
  }, []);
  const ariaOpenProfileImagePicker = useOnEnterKey(openProfileImagePicker);

  const [updateProfile, isUpdatingProfile] =
    useMutation<ProfilesMutation>(EditProfile);

  const updateBanner = () => {
    if (!form.current || !form.current.elements.aboutBanner.files.length)
      return;
    setCurrentBanner(form.current.elements.aboutBanner.files[0]);
  };

  const updateProfileImage = () => {
    if (!form.current || !form.current.elements.profileImage.files.length)
      return;
    setCurrentProfileImage(form.current.elements.profileImage.files[0]);
  };

  const formikPropsRef = useRef(defaultFormikProps);
  const [isDirty, setIsDirty] = useState(false);
  const [hasErrors, setHasErrors] = useState(false);

  const submitButton = (
    <MPActionButton
      size="large"
      onClick={formikPropsRef.current.handleSubmit}
      isLoading={isUpdatingProfile}
      disabled={!isDirty || hasErrors}
    >
      Update
    </MPActionButton>
  );

  const socialLinks = profileData.socialLinks.reduce((linkMap, link) => {
    /*  eslint-disable-next-line no-param-reassign */
    linkMap[link.linkType] = link.value;
    return linkMap;
  }, {});

  const resetInputs = () => {
    setCurrentBanner(null);
    setCurrentProfileImage(null);
    if (form.current) {
      form.current.elements.aboutBanner.value = null;
      form.current.elements.profileImage.value = null;
    }
  };

  const isMobile = useIsMobile();

  return (
    <MPDialog
      onClose={cancel}
      classes={{ root: styles.EditProfileDialog }}
      open={isOpen}
      actionButton={submitButton}
      sx={{
        '& .MuiDialog-paper': {
          maxHeight: 'var(--maxDialogHeight)',
          width: isMobile ? '100%' : '502px',
        },
      }}
      title="Edit Profile"
    >
      <Formik
        initialValues={{
          artstationLink: socialLinks[StoreLinkLinkType.Artstation] ?? '',
          behanceLink: socialLinks[StoreLinkLinkType.Behance] ?? '',
          description: profileData.description ?? '',
          discordUserLink: socialLinks[StoreLinkLinkType.DiscordUser] ?? '',
          displayName: profileData.displayName,
          facebookLink: socialLinks[StoreLinkLinkType.Facebook] ?? '',
          instagramLink: socialLinks[StoreLinkLinkType.Instagram] ?? '',
          pinterestLink: socialLinks[StoreLinkLinkType.Pinterest] ?? '',
          shortDescription: profileData.shortDescription ?? '',
          steemitLink: socialLinks[StoreLinkLinkType.Steemit] ?? '',
          twitterLink: socialLinks[StoreLinkLinkType.Twitter] ?? '',
          websiteLink: sanitizeUrl(
            socialLinks[StoreLinkLinkType.Website] ?? ''
          ),
          youtubeChannelLink:
            socialLinks[StoreLinkLinkType.YoutubeChannel] ?? '',
        }}
        validate={validateForm}
        enableReinitialize
        onSubmit={(values, { setSubmitting, setStatus, setFieldError }) => {
          if (isUpdatingProfile) return;
          const variables: ProfileInput = {
            artstationLink: values.artstationLink,
            behanceLink: values.behanceLink,
            discordUserLink: values.discordUserLink,
            facebookLink: values.facebookLink,
            instagramLink: values.instagramLink,
            pinterestLink: values.pinterestLink,
            steemitLink: values.steemitLink,
            twitterLink: values.twitterLink,
            websiteLink: values.websiteLink,
            youtubeChannelLink: values.youtubeChannelLink,
          };
          if (profileData.shortDescription !== values.shortDescription)
            variables.shortDescription = values.shortDescription;
          if (profileData.description !== values.description)
            variables.description = values.description;
          if (
            values.displayName &&
            profileData.displayName !== values.displayName
          )
            variables.displayName = values.displayName;
          const uploadables: UploadableMap = {};
          if (currentBanner) uploadables.aboutBanner = currentBanner;
          if (currentProfileImage)
            uploadables.profileImage = currentProfileImage;

          updateProfile({
            onCompleted: (resp) => {
              resetInputs();
              setSubmitting(false);
              setOpen(false);
              if (resp?.editProfile?.newSlug) {
                navigate(`/${resp.editProfile.newSlug}`);
              }
            },
            onError: (error) => {
              if (
                error.name === MpErrors.SlugEthereum ||
                error.name === MpErrors.SlugConflict
              ) {
                setFieldError('slug', error.message);
              } else {
                setStatus(error.message);
              }
            },
            uploadables,
            variables,
          });
          setStatus('');
          setSubmitting(true);
        }}
      >
        {(props) => {
          formikPropsRef.current = props;

          // This should be fine since the callback isn't conditionally called.
          /* eslint-disable-next-line react-hooks/rules-of-hooks */
          useEffect(() => {
            setHasErrors(
              validateForm(formikPropsRef.current.values) !== undefined
            );
            // The outer scope variable should be tied with formik's internal form state.
            /* eslint-disable-next-line react-hooks/exhaustive-deps */
          });

          // This should be fine since the callback isn't conditionally called.
          /* eslint-disable-next-line react-hooks/rules-of-hooks */
          useEffect(() => {
            setIsDirty(props.dirty || currentBanner || currentProfileImage);
            // The outer scope variable should be tied with formik's internal form state.
            /* eslint-disable-next-line react-hooks/exhaustive-deps */
          }, [props.dirty, currentBanner, currentProfileImage]);

          return (
            <form ref={form}>
              <div className={joinClasses(styles.dialogPadding)}>
                <div
                  className={joinClasses(
                    MPFonts.textSmallSemiBold,
                    CSSGlobal.Flex.RowCenterAlign
                  )}
                >
                  Banner Image&nbsp;
                  <MPTooltip
                    title="Max Size is 10mb. Recommended Aspect Ratio is 5:1"
                    placement="bottom"
                  >
                    <InfoIcon
                      htmlColor={MPColorValue.SolidNeutralGray3 as string}
                      fontSize="inherit"
                    />
                  </MPTooltip>
                </div>
                <div className={styles.ProfileContainer}>
                  <div
                    className={joinClasses(
                      styles.addImageIconWrapper,
                      styles.banner
                    )}
                  >
                    <NotifySaleIcon
                      className={styles.addImageIcon}
                      htmlColor={MPColorValue.CommonWhite}
                      onClick={openBannerPicker}
                      onKeyPress={ariaOpenBannerPicker}
                      aria-label="Upload Banner Image"
                      role="button"
                      tabIndex={0}
                    />
                  </div>
                  <ProfileImg backgroundImage={bannerImage} />
                  <div
                    className={styles.hoverOverlay}
                    onClick={openBannerPicker}
                    onKeyPress={ariaOpenBannerPicker}
                    aria-label="Upload Banner Image"
                    role="button"
                    tabIndex={0}
                  >
                    <div className={joinClasses(bannerStyles.image)}>
                      <ProfileImg backgroundImage={bannerImage} />
                    </div>
                    <div className={styles.hoverOverlayMaskContainer} />
                  </div>
                </div>
                <div
                  className={joinClasses(
                    CSSGlobal.Flex.Col,
                    CSSIn.GAP[8],
                    styles.profileImageContainer
                  )}
                >
                  <div
                    className={joinClasses(
                      MPFonts.textSmallSemiBold,
                      CSSGlobal.Flex.RowCenterAlign
                    )}
                  >
                    Profile Image&nbsp;
                    <MPTooltip title="Max Size is 10mb." placement="bottom">
                      <InfoIcon
                        htmlColor={MPColorValue.SolidNeutralGray3 as string}
                        fontSize="inherit"
                      />
                    </MPTooltip>
                  </div>
                  <div
                    onClick={openProfileImagePicker}
                    onKeyPress={ariaOpenProfileImagePicker}
                    className={styles.profilePhotoWrapper}
                    aria-label="Upload Profile Image"
                    role="button"
                    tabIndex={0}
                  >
                    <img className={styles.profilePhoto} src={profileImage} />
                    <div className={styles.addImageIconWrapper}>
                      <NotifySaleIcon
                        className={styles.addImageIcon}
                        htmlColor={MPColorValue.CommonWhite}
                      />
                    </div>
                  </div>
                </div>
                <input
                  name="aboutBanner"
                  type="file"
                  ref={bannerInputRef}
                  style={{ display: 'none' }}
                  onChange={updateBanner}
                />
                <input
                  name="profileImage"
                  type="file"
                  ref={profileInputRef}
                  style={{ display: 'none' }}
                  onChange={updateProfileImage}
                />
                <div className={joinClasses(CSSGlobal.Flex.Col, CSSGap[24])}>
                  {!!props.status && (
                    <div className={inputStyles.errorMessage}>
                      {props.status}
                    </div>
                  )}
                  <MPStyledTextField
                    label="Display Name"
                    name="displayName"
                    onChange={props.handleChange}
                    value={props.values.displayName}
                    placeholder="This name will be displayed on your profile"
                  />
                  <MPStyledTextField
                    label={
                      <div className={CSSGlobal.Flex.RowSpaceBetween}>
                        <span>Headline</span>
                        <span
                          className={joinClasses(
                            props.errors?.shortDescription
                              ? MPColorClass.ErrorMain
                              : MPColorClass.SolidNeutralGray3
                          )}
                        >
                          {charCount(props.values.shortDescription)}/170
                        </span>
                      </div>
                    }
                    rows={2}
                    name="shortDescription"
                    onChange={props.handleChange}
                    onBlur={props.handleBlur}
                    value={props.values.shortDescription}
                    placeholder="This short description will be displayed on your profile"
                  />
                  <MPStyledTextField
                    label="About"
                    rows={5}
                    name="description"
                    onChange={props.handleChange}
                    onBlur={props.handleBlur}
                    value={props.values.description}
                    placeholder="Your personal statement will be displayed within the about tab"
                  />
                  <div className={joinClasses(CSSGlobal.Flex.Col, CSSGap[10])}>
                    <span className={MPFonts.textSmallSemiBold}>
                      Your Links
                    </span>
                    <SocialMediaTextField
                      fieldValue="websiteLink"
                      handleChange={props.handleChange}
                      onBlur={props.handleBlur}
                      setFieldValue={props.setFieldValue}
                      placeholder="Your Personal Website"
                      value={props.values.websiteLink}
                    />
                    <SocialMediaTextField
                      prefix={TWITTER_PROTOCOLLESS_BASE_URL}
                      fieldValue="twitterLink"
                      handleChange={props.handleChange}
                      onBlur={props.handleBlur}
                      setFieldValue={props.setFieldValue}
                      value={props.values.twitterLink}
                    />
                    <SocialMediaTextField
                      prefix={DISCORD_PROTOCOLLESS_USR_BASE_URL}
                      fieldValue="discordUserLink"
                      handleChange={props.handleChange}
                      onBlur={props.handleBlur}
                      setFieldValue={props.setFieldValue}
                      value={props.values.discordUserLink}
                    />
                    <SocialMediaTextField
                      prefix={INSTAGRAM_PROTOCOLLESS_BASE_URL}
                      fieldValue="instagramLink"
                      handleChange={props.handleChange}
                      onBlur={props.handleBlur}
                      setFieldValue={props.setFieldValue}
                      value={props.values.instagramLink}
                    />
                  </div>
                </div>
              </div>
            </form>
          );
        }}
      </Formik>
    </MPDialog>
  );
}
