import { PreloadedQuery, usePreloadedQuery } from 'react-relay';

import GetFollowers, {
  FollowersQuery,
} from 'graphql/__generated__/FollowersQuery.graphql';

export type useFollowersDataQueryRef = PreloadedQuery<FollowersQuery>;

export default function useFollowersData(
  followQueryRef: useFollowersDataQueryRef
) {
  return usePreloadedQuery<FollowersQuery>(GetFollowers, followQueryRef)
    .followers;
}
