import {
  MPBackgroundColorClass,
  MPColorValue,
  MPTooltip,
  MPTooltipProps,
} from '@mp-frontend/core-components';
import { CheckCircleIcon } from '@mp-frontend/core-components/icons';
import { joinClasses } from '@mp-frontend/core-utils';

import { ProfileAccountType } from 'types/__generated__/graphql';

import VerifiedTooltip from './VerifiedTooltip';

interface CollectionBadgeProps
  extends Omit<MPTooltipProps, 'children' | 'title'> {
  enterDelay?: number;
  leaveDelay?: number;
  placement?: MPTooltipProps['placement'];
  variant?: MPTooltipProps['variant'];
}

export default function CollectionBadge({
  placement = 'right',
  enterDelay = 500,
  leaveDelay = 500,
  variant,
  className,
  ...props
}: CollectionBadgeProps) {
  return (
    <MPTooltip
      title={<VerifiedTooltip accountType={ProfileAccountType.Collection} />}
      placement={placement}
      enterDelay={enterDelay}
      leaveDelay={leaveDelay}
      variant={variant}
      {...props}
    >
      <CheckCircleIcon
        fontSize="24"
        htmlColor={MPColorValue.GoldMain as string}
        className={joinClasses(className, MPBackgroundColorClass.CommonWhite)}
      />
    </MPTooltip>
  );
}
