import { useMemo } from 'react';

import { useIsMobile } from '@mp-frontend/core-components';
import {
  CopyLinkIcon,
  DiscordIcon,
  InstagramIcon,
  TwitterIcon,
} from '@mp-frontend/core-components/icons';

import { StoreLinkLinkType } from 'types/__generated__/graphql';

import {
  DISCORD_USR_BASE_URL,
  INSTAGRAM_BASE_URL,
  TWITTER_BASE_URL,
} from 'constants/ExternalUrls';
import ensureHTTPBasedProtocol from 'utils/ensureHTTPBasedProtocol';
import sanitizeUrl from 'utils/sanitizeUrl';

import { useFollowersDataQueryRef } from '../useFollowersData';
import useProfileData, { useProfileDataQueryRef } from '../useProfileData';
import DesktopHeader from './desktop';
import MobileHeader from './mobile';

interface ProfileHeaderProps {
  followQueryRef: useFollowersDataQueryRef;
  profileQueryRef: useProfileDataQueryRef;
}

const MAX_DESCRIPTION_LENGTH = 170;

const OrderedSocialMediaMap = [
  {
    Icon: CopyLinkIcon,
    baseUrl: '',
    linkType: StoreLinkLinkType.Website,
    tooltip: 'Personal Website',
  },
  {
    Icon: TwitterIcon,
    baseUrl: TWITTER_BASE_URL,
    linkType: StoreLinkLinkType.Twitter,
    tooltip: 'Twitter',
  },
  {
    Icon: InstagramIcon,
    baseUrl: INSTAGRAM_BASE_URL,
    linkType: StoreLinkLinkType.Instagram,
    tooltip: 'Instagram',
  },
  {
    Icon: DiscordIcon,
    baseUrl: DISCORD_USR_BASE_URL,
    linkType: StoreLinkLinkType.DiscordUser,
    tooltip: 'Discord',
  },
];

export default function ProfileHeader({
  profileQueryRef,
  followQueryRef,
}: ProfileHeaderProps) {
  const profileData = useProfileData(profileQueryRef);
  const isMobile = useIsMobile();
  const trimmedDescription = useMemo(() => {
    const text = profileData.shortDescription ?? '';
    if (text.length > MAX_DESCRIPTION_LENGTH) {
      return `${text.slice(0, MAX_DESCRIPTION_LENGTH + 1)}...`;
    }
    return text;
  }, [profileData.shortDescription]);

  const socialLinksMap = profileData.socialLinks.reduce((linkMap, link) => {
    /*  eslint-disable-next-line no-param-reassign */
    linkMap[link.linkType] = link.value;
    return linkMap;
  }, {});

  const socialLinks = OrderedSocialMediaMap.filter(
    (socialMedia) => socialMedia.linkType in socialLinksMap
  ).map((socialMedia) => ({
    ...socialMedia,
    href: socialMedia.baseUrl
      ? `${socialMedia.baseUrl}${socialLinksMap[socialMedia.linkType]}`
      : sanitizeUrl(
          ensureHTTPBasedProtocol(socialLinksMap[socialMedia.linkType])
        ) ?? 'about:blank',
  }));

  const Header = isMobile ? MobileHeader : DesktopHeader;

  return (
    <Header
      profileQueryRef={profileQueryRef}
      description={trimmedDescription}
      socialLinks={socialLinks}
      followQueryRef={followQueryRef}
    />
  );
}
