import { useCallback, useState } from 'react';
import { useParams } from 'react-router-dom';

import { MPActionButton } from '@mp-frontend/core-components';

import { ProfileAccountType } from 'types/__generated__/graphql';

import { FollowButton } from 'components/buttons/FollowButton';

import EditProfileDialog from './dialog/EditProfile';
import useProfileData, { useProfileDataQueryRef } from './useProfileData';

interface IProps {
  profileQueryRef: useProfileDataQueryRef;
}

function FollowButtonWrapper({ profileQueryRef }: IProps) {
  const { profileSlug } = useParams();
  const profileData = useProfileData(profileQueryRef);
  return profileData.accountType !== ProfileAccountType.Collection &&
    profileData.user.pk ? (
    <FollowButton
      username={profileSlug}
      userId={parseInt(profileData.user.pk, 10)}
    />
  ) : (
    <></>
  );
}

function ProfileActionButtonSection({ profileQueryRef }: IProps) {
  const [isEditProfileDialogOpen, setEditProfileDialogOpen] = useState(false);
  const openEditProfileDialogOpen = useCallback(
    () => setEditProfileDialogOpen(true),
    [setEditProfileDialogOpen]
  );
  const profileData = useProfileData(profileQueryRef);
  return profileData.isCurrentUser ? (
    <>
      <MPActionButton variant="tertiary" onClick={openEditProfileDialogOpen}>
        Edit&nbsp;Profile
      </MPActionButton>
      <EditProfileDialog
        profileQueryRef={profileQueryRef}
        isOpen={isEditProfileDialogOpen}
        setOpen={setEditProfileDialogOpen}
      />
    </>
  ) : (
    <FollowButtonWrapper profileQueryRef={profileQueryRef} />
  );
}

export default ProfileActionButtonSection;
