import { ProfileAccountType } from 'types/__generated__/graphql';

import CollectionBadge from 'components/badges/CollectionBadge';
import CreatorBadge from 'components/badges/CreatorBadge';

interface ProfileBadgeProps {
  accountType: ProfileAccountType;
  className?: string;
}

export default function ProfileBadge({
  accountType,
  className,
}: ProfileBadgeProps) {
  switch (accountType) {
    case ProfileAccountType.Creator:
      return <CreatorBadge className={className} />;
    case ProfileAccountType.Collection:
      return <CollectionBadge className={className} />;
    default:
      return null;
  }
}
