import { joinClasses } from '@mp-frontend/core-utils';

import { ProfileAccountType } from 'types/__generated__/graphql';

import User from 'components/accounts/User';
import ProfileCollectionActionSection from 'pages/profiles/ProfileCollectionActionSection';
import CSSGap from 'types/enums/css/Gap';
import CSSGlobal from 'types/enums/css/Global';

import useProfileData from '../../useProfileData';
import ProfileMarketInsights from '../ProfileMarketInsights';
import { HeaderProps } from '../types';
import LinkActions from './LinkActions';
import Title from './Title';

import * as styles from 'css/pages/profiles/header/desktop/CollectionHeader.module.css';
import * as generalStyles from 'css/pages/profiles/ProfileHeader.module.css';

export default function CollectionUserHeader({
  profileQueryRef,
  socialLinks,
}: HeaderProps) {
  const profileData = useProfileData(profileQueryRef);

  return (
    <div className={generalStyles.collectionHeader}>
      <div
        className={joinClasses(
          generalStyles.firstRow,
          generalStyles.isCollection
        )}
      >
        <div className={joinClasses(CSSGlobal.Flex.Col)}>
          <div className={styles.creator}>
            <User user={profileData.user} />
          </div>
          <Title
            displayName={profileData.displayName}
            accountType={profileData.accountType}
          />
          <div
            className={joinClasses(generalStyles.actionRowLeft, {
              [generalStyles.alignSelfStart]:
                profileData.accountType === ProfileAccountType.Collection,
            })}
          >
            {!!profileData.drop?.slug && (
              <ProfileCollectionActionSection drop={profileData.drop} />
            )}
          </div>
        </div>
        <div
          className={joinClasses(
            CSSGlobal.Flex.Col,
            CSSGap[16],
            styles.rightBlock
          )}
        >
          <LinkActions
            profileQueryRef={profileQueryRef}
            socialLinks={socialLinks}
          />
          <ProfileMarketInsights profileData={profileData} />
        </div>
      </div>
    </div>
  );
}
