import { MPFonts } from '@mp-frontend/core-components';
import { joinClasses } from '@mp-frontend/core-utils';

import { ProfileAccountType } from 'types/__generated__/graphql';

import ProfileFollowers from 'pages/profiles/followers';
import ProfileActionButtonSection from 'pages/profiles/ProfileActionButton';
import CSSGlobal from 'types/enums/css/Global';
import getProfileImage from 'utils/getProfileImage';

import useProfileData from '../../useProfileData';
import ProfileMarketInsights from '../ProfileMarketInsights';
import { HeaderProps } from '../types';
import LinkActions from './LinkActions';
import Title from './Title';

import * as styles from 'css/pages/profiles/ProfileHeader.module.css';

export default function DesktopUserHeader({
  profileQueryRef,
  description,
  socialLinks,
  followQueryRef,
}: HeaderProps) {
  const profileData = useProfileData(profileQueryRef);

  return (
    <div className={styles.collectionHeader}>
      <div className={joinClasses(CSSGlobal.Flex.Row, styles.firstRow)}>
        <div
          className={joinClasses(CSSGlobal.Flex.Col, 'reenablePointerEvents')}
        >
          <img
            className={styles.profilePhoto}
            src={getProfileImage(profileData.profileImageUrl)}
          />
        </div>
        <div className={styles.profileInfoSection}>
          <div
            className={joinClasses(CSSGlobal.Flex.Row, styles.titleSocialRow)}
          >
            <Title
              displayName={profileData.displayName}
              accountType={profileData.accountType}
            />
            <LinkActions
              profileQueryRef={profileQueryRef}
              socialLinks={socialLinks}
            />
          </div>
          <div
            className={joinClasses(
              CSSGlobal.Flex.Row,
              styles.actionRow,
              CSSGlobal.Flex.NoWrap
            )}
          >
            <div
              className={joinClasses(styles.actionRowLeft, {
                [styles.alignSelfStart]:
                  profileData.accountType === ProfileAccountType.Collection,
              })}
            >
              <ProfileActionButtonSection profileQueryRef={profileQueryRef} />
              <ProfileFollowers followQueryRef={followQueryRef} />
            </div>
            <div className={styles.actionRowRight}>
              <ProfileMarketInsights profileData={profileData} />
            </div>
          </div>
        </div>
      </div>
      <div className={joinClasses(MPFonts.paragraphNormal, styles.description)}>
        <div className={styles.trimmedDescription}>{description}</div>
      </div>
    </div>
  );
}
