import { joinClasses } from '@mp-frontend/core-utils';

import { ProfileAccountType } from 'types/__generated__/graphql';

import { ProfileImg } from './banner';
import useProfileData, { useProfileDataQueryRef } from './useProfileData';

import * as styles from 'css/pages/profiles/ProfileBanner.module.css';

interface ProfilesBannerProps {
  profileQueryRef: useProfileDataQueryRef;
}

export default function ProfilesBanner({
  profileQueryRef,
}: ProfilesBannerProps) {
  const profileData = useProfileData(profileQueryRef);
  const isCollection =
    profileData.accountType === ProfileAccountType.Collection;

  return (
    <div
      className={joinClasses(styles.idBaseHeaderContainer, {
        [styles.collection]: isCollection,
      })}
    >
      {!isCollection && (
        <ProfileImg backgroundImage={profileData.aboutBanner} />
      )}
    </div>
  );
}
