import { HTMLAttributes, useCallback, useState } from 'react';
import { useQueryLoader } from 'react-relay';

import { MPColorClass, MPFonts } from '@mp-frontend/core-components';
import { joinClasses } from '@mp-frontend/core-utils';

import GetFollowers, {
  GetFollowersQuery,
} from 'graphql/__generated__/GetFollowersQuery.graphql';

import useOnEnterKey from 'utils/eventHandlers/useOnEnterKey';
import toNumericShorthand from 'utils/numericShorthand';

import { FollowersDialog } from '../dialog/Followers';
import useFollowersData, {
  useFollowersDataQueryRef,
} from '../useFollowersData';

import * as styles from 'css/pages/profiles/followers/index.module.css';

interface ProfileFollowersProps extends HTMLAttributes<HTMLDivElement> {
  followQueryRef: useFollowersDataQueryRef;
}

export default function ProfileFollowers({
  followQueryRef,
  ...props
}: ProfileFollowersProps) {
  const [isFollowersDialogOpen, setIsFollowersDialogOpen] = useState(false);
  const [getFollowersQueryRef, loadFollowersQuery] =
    useQueryLoader<GetFollowersQuery>(GetFollowers);
  const openFollowersDialog = useCallback(
    () => setIsFollowersDialogOpen(true),
    [setIsFollowersDialogOpen]
  );
  const ariaOpenFollowersDialog = useOnEnterKey(openFollowersDialog);

  const followersData = useFollowersData(followQueryRef);

  return (
    <div {...props}>
      <span
        className={joinClasses(
          MPFonts.textSmallMedium,
          MPColorClass.SolidNeutralGray5
        )}
      >
        {followersData.followers > 0 && (
          <span
            className={styles.followUserLink}
            onClick={openFollowersDialog}
            onKeyPress={ariaOpenFollowersDialog}
            aria-label="view Followers"
            role="button"
            tabIndex={0}
          >
            {toNumericShorthand(followersData.followers)}&nbsp;Followers
          </span>
        )}
      </span>
      <FollowersDialog
        title="Followers"
        isOpen={isFollowersDialogOpen}
        setOpen={setIsFollowersDialogOpen}
        queryRef={getFollowersQueryRef}
        loadQuery={loadFollowersQuery}
      />
    </div>
  );
}
