import { ProfileAccountType } from 'types/__generated__/graphql';

import useProfileData from '../../useProfileData';
import { HeaderProps } from '../types';
import CollectionHeader from './CollectionHeader';
import UserHeader from './UserHeader';

export default function DesktopHeader({
  profileQueryRef,
  ...props
}: HeaderProps) {
  const profileData = useProfileData(profileQueryRef);
  const Header =
    profileData.accountType === ProfileAccountType.Collection
      ? CollectionHeader
      : UserHeader;

  return <Header profileQueryRef={profileQueryRef} {...props} />;
}
