import { useCallback, useEffect, useRef, useState } from 'react';

import { MPFonts } from '@mp-frontend/core-components';
import { CheckIcon, CopyIcon } from '@mp-frontend/core-components/icons';
import { joinClasses } from '@mp-frontend/core-utils';

import CSSIn from 'types/enums/css';
import CSSGlobal from 'types/enums/css/Global';
import useOnEnterKey from 'utils/eventHandlers/useOnEnterKey';

import useProfileData, { useProfileDataQueryRef } from '../useProfileData';
import useWallet from './useWallet';

import * as styles from 'css/pages/profiles/walletAddress/index.module.css';

interface WalletAddressProps {
  profileQueryRef: useProfileDataQueryRef;
  className?: string;
}

export default function WalletAddress({
  profileQueryRef,
  className,
}: WalletAddressProps) {
  const profileData = useProfileData(profileQueryRef);
  const [walletAddress, copyWalletAddress] = useWallet(profileData.address);
  const [wasCopied, setWasCopied] = useState(false);
  const wasCopiedTimeoutRef = useRef<number>(null);
  const copy = useCallback(() => {
    if (wasCopied) return;
    copyWalletAddress();
    setWasCopied(true);
    wasCopiedTimeoutRef.current = window.setTimeout(
      () => setWasCopied(false),
      2000
    );
  }, [copyWalletAddress, wasCopied]);

  const ariaCopyWalletAddress = useOnEnterKey(copy);

  useEffect(() => () => window.clearTimeout(wasCopiedTimeoutRef.current), []);

  if (!walletAddress) return <></>;

  return (
    <span
      onClick={copy}
      onKeyPress={ariaCopyWalletAddress}
      aria-label="Copy Wallet Address"
      role="button"
      tabIndex={0}
      className={joinClasses(
        CSSGlobal.Flex.RowCenterAlign,
        CSSIn.GAP[4],
        MPFonts.textSmallMedium,
        styles.walletAddress,
        className
      )}
    >
      {wasCopied ? (
        <CheckIcon fontSize="inherit" className={styles.checkIcon} />
      ) : (
        <CopyIcon />
      )}
      <span className={styles.text}>
        {wasCopied ? <>Copied&nbsp;to&nbsp;Clipboard</> : walletAddress}
      </span>
    </span>
  );
}
