import { useCallback, useEffect, useRef } from 'react';
import { useQueryLoaderHookType } from 'react-relay/relay-hooks/useQueryLoader';
import {
  PreloadedQuery,
  usePaginationFragment,
  usePreloadedQuery,
} from 'react-relay';
import { Link, useParams } from 'react-router-dom';
import { GraphQLTaggedNode, OperationType } from 'relay-runtime';

import { MPDialog, useIsMobile } from '@mp-frontend/core-components';
import { joinClasses } from '@mp-frontend/core-utils';

import FollowButton from 'components/buttons/FollowButton';
import useInfiniteQueryScroll from 'hooks/useInfiniteQueryScroll';

import * as styles from 'css/pages/profiles/dialog/Followers/dialog.module.css';

interface FollowDialogInnerProps<TQuery extends OperationType> {
  fragmentInput;
  getQuery: GraphQLTaggedNode;
  queryRef: PreloadedQuery<TQuery>;
}

function FollowDialogInner<TQuery extends OperationType & { response: any }>({
  fragmentInput,
  queryRef,
  getQuery,
}: FollowDialogInnerProps<TQuery>) {
  const visibilityRef = useRef(null);
  const scrollRef = useRef(null);

  const { data } = useInfiniteQueryScroll({
    pageSize: 10,
    paginatedQueryResults: usePaginationFragment<TQuery, TQuery['response']>(
      fragmentInput,
      usePreloadedQuery<TQuery>(getQuery, queryRef)
    ),
    ref: visibilityRef,
    scrollRef,
  });

  return (
    <div className={styles.scrollContainer} ref={scrollRef}>
      {data.map((followUser) => (
        <div className={styles.followUserRow} key={followUser.username}>
          <Link
            to={followUser.landingUrl}
            className={joinClasses(styles.followUserLink, 'invisibleAnchor')}
          >
            <img
              className={styles.profileImage}
              src={followUser.profileImageUrl}
            />
            <span className="textOverflowEllipsis">{followUser.fullName}</span>
          </Link>
          <FollowButton
            size="small"
            username={followUser.username}
            isFollowing={followUser.isFollowing}
          />
        </div>
      ))}
      <div ref={visibilityRef}>&nbsp;</div>
    </div>
  );
}

interface FolllowersDialogProps<TQuery extends OperationType> {
  isOpen: boolean;
  loadQuery: useQueryLoaderHookType<TQuery>[1];
  queryRef: useQueryLoaderHookType<TQuery>[0];
  setOpen: (open: boolean) => void;
  title: string;
}

export default function createFollowUserDialog<TQuery extends OperationType>(
  fragmentInput: GraphQLTaggedNode,
  getQuery
) {
  return function AnonFollowUserDialog({
    title,
    isOpen,
    setOpen,
    queryRef,
    loadQuery,
  }: FolllowersDialogProps<TQuery>) {
    const { profileSlug } = useParams();
    const close = useCallback(() => {
      setOpen(false);
    }, [setOpen]);

    useEffect(() => {
      if (!isOpen || queryRef) return;
      loadQuery({ first: 10, globalSlug: profileSlug });
    }, [isOpen, queryRef, loadQuery, profileSlug]);

    const isMobile = useIsMobile();

    return (
      <MPDialog
        onClose={close}
        classes={{ root: styles.dialog }}
        open={isOpen}
        sx={{
          '& .MuiDialog-paper': {
            maxHeight: 'var(--maxDialogHeight)',
            width: isMobile ? '100%' : '600px',
          },
        }}
        title={title}
      >
        {!!queryRef && (
          <FollowDialogInner<TQuery>
            fragmentInput={fragmentInput}
            queryRef={queryRef}
            getQuery={getQuery}
          />
        )}
      </MPDialog>
    );
  };
}
