import { useEffect } from 'react';
import { useSubscribeToInvalidationState } from 'react-relay';
import { Outlet, useParams } from 'react-router-dom';

import GetFollowers, {
  FollowersQuery,
} from 'graphql/__generated__/FollowersQuery.graphql';
import GetProfile, {
  ProfilesQuery,
} from 'graphql/__generated__/ProfilesQuery.graphql';
import { ProfileAccountType } from 'types/__generated__/graphql';

import DefaultSuspense from 'components/DefaultSuspense';
import RedirectErrorBoundary from 'components/ErrorBoundaries/RedirectErrorBoundary';
import { APP_NAME } from 'constants/Utils';
import useLoadQuery from 'hooks/useLoadQuery';
import withShopOrActivityCheck from 'utils/hocs/withProfileShopActivityTab';
import setDocTitle from 'utils/setDocTitle';

import ProfileHeader from './header';
import ProfileBanner from './ProfileBanner';
import useProfileData, { useProfileDataQueryRef } from './useProfileData';

import * as styles from 'css/pages/profiles/ProfilePage.module.css';

interface ProfileContentProps {
  invalidate: () => void;
  profileQueryRef: useProfileDataQueryRef;
}

function ProfileContent({ profileQueryRef, invalidate }: ProfileContentProps) {
  const { profileSlug, categorySlug } = useParams();
  const [followQueryRef] = useLoadQuery<FollowersQuery>(GetFollowers, {
    global_slug: profileSlug,
  });
  const profileData = useProfileData(profileQueryRef);

  useSubscribeToInvalidationState([profileData.id], invalidate);
  useEffect(() => {
    if (profileData.fullName)
      setDocTitle(`
        ${
          categorySlug
            ? `${profileData.fullName} | ${categorySlug}`
            : profileData.fullName
        } | ${APP_NAME}`);
  }, [profileData.fullName, categorySlug]);

  return (
    <>
      <div className={styles.profilePageContent}>
        <ProfileBanner profileQueryRef={profileQueryRef} />
        <div className={styles.profileBody}>
          {!!followQueryRef && (
            <ProfileHeader
              profileQueryRef={profileQueryRef}
              followQueryRef={followQueryRef}
            />
          )}
          <DefaultSuspense>
            <div className={styles.subpageContent}>
              {!!followQueryRef && (
                <Outlet
                  context={{
                    defaultRedirectSlug:
                      profileData.accountType === ProfileAccountType.Collection
                        ? 'shop'
                        : 'gallery',
                    followQueryRef,
                    profileQueryRef,
                  }}
                />
              )}
            </div>
          </DefaultSuspense>
        </div>
      </div>
    </>
  );
}
const WrappedProfileContent = withShopOrActivityCheck(ProfileContent);

function ProfilePage() {
  const { profileSlug } = useParams();
  const [profileQueryRef, , , invalidate] = useLoadQuery<ProfilesQuery>(
    GetProfile,
    {
      slug: profileSlug,
    }
  );

  return (
    <div>
      <RedirectErrorBoundary>
        {!!profileQueryRef && (
          <WrappedProfileContent
            profileQueryRef={profileQueryRef}
            invalidate={invalidate}
          />
        )}
      </RedirectErrorBoundary>
    </div>
  );
}

export default ProfilePage;
