import {
  MPBackgroundColorClass,
  MPColorValue,
  MPTooltip,
  MPTooltipProps,
  useIsMobile,
} from '@mp-frontend/core-components';
import { CheckCircleIcon } from '@mp-frontend/core-components/icons';
import { joinClasses } from '@mp-frontend/core-utils';

import { ProfileAccountType } from 'types/__generated__/graphql';

import VerifiedTooltip from './VerifiedTooltip';

interface CreatorBadgeProps extends Omit<MPTooltipProps, 'children' | 'title'> {
  enterDelay?: number;
  leaveDelay?: number;
  placement?: MPTooltipProps['placement'];
  variant?: MPTooltipProps['variant'];
}

export default function CreatorBadge({
  placement = 'right',
  enterDelay = 500,
  leaveDelay = 500,
  variant,
  className,
  ...props
}: CreatorBadgeProps) {
  const isMobile = useIsMobile();

  return (
    <MPTooltip
      title={<VerifiedTooltip accountType={ProfileAccountType.Creator} />}
      placement={placement}
      enterDelay={enterDelay}
      leaveDelay={leaveDelay}
      variant={variant}
      {...props}
    >
      <CheckCircleIcon
        fontSize={isMobile ? '17' : '20'}
        htmlColor={MPColorValue.GoldMain as string}
        className={joinClasses(
          className,
          MPBackgroundColorClass.TransparentPrimary
        )}
      />
    </MPTooltip>
  );
}
