import {
  MPColorClass,
  MPFonts,
  useIsMobile,
} from '@mp-frontend/core-components';
import { joinClasses } from '@mp-frontend/core-utils';

import { ProfileAccountType } from 'types/__generated__/graphql';

import MarketInsights from 'components/MarketInsights';
import CSSGlobal from 'types/enums/css/Global';

import useProfileData from '../useProfileData';

import * as styles from 'css/pages/profiles/header/ProfileMarketInsights.module.css';

interface ProfileMarketInsightsProps {
  profileData: ReturnType<typeof useProfileData>;
}

export default function ProfileMarketInsights({
  profileData,
}: ProfileMarketInsightsProps) {
  const isMobile = useIsMobile();

  return profileData.accountType === ProfileAccountType.Creator ||
    profileData.accountType === ProfileAccountType.Collection ? (
    <MarketInsights
      replacementClassName={joinClasses(styles.insights, CSSGlobal.Flex.NoWrap)}
      replacementEntryClassName={styles.insight}
      replacementLabelClassName={joinClasses(
        isMobile ? MPFonts.textNormalMedium : MPFonts.textSmallMedium,
        MPColorClass.SolidNeutralGray5
      )}
      replacementValueClassName={
        isMobile ? MPFonts.subPrice : MPFonts.textNormalMedium
      }
      floorPrice={{
        floorPriceInEth: profileData.listingStats.floorPrice.floorPriceInEth,
        floorPriceInUsd: profileData.listingStats.floorPrice.floorPriceInUsd,
      }}
      numOfOwners={profileData.numOfOwners}
      totalItems={profileData.listingStats.totalItems}
      totalVolume={{
        totalVolumeInEth: profileData.totalVolume.totalVolumeInEth,
        totalVolumeInUsd: profileData.totalVolume.totalVolumeInUsd,
      }}
      numOfItemsListed={profileData.numOfItemsListed}
    />
  ) : (
    <></>
  );
}
