import { MPColorClass } from '@mp-frontend/core-components';
import { EtherscanIcon, OpenSeaIcon } from '@mp-frontend/core-components/icons';
import { joinClasses } from '@mp-frontend/core-utils';

import WalletAddress from 'pages/profiles/walletAddress';
import CSSGap from 'types/enums/css/Gap';

import useProfileData from '../../useProfileData';
import { HeaderProps } from '../types';
import SocialIconLink from './SocialIconLink';

import * as styles from 'css/pages/profiles/ProfileHeader.module.css';

export default function LinkActions({
  profileQueryRef,
  socialLinks,
}: Pick<HeaderProps, 'profileQueryRef' | 'socialLinks'>) {
  const profileData = useProfileData(profileQueryRef);

  return (
    <div
      className={joinClasses(
        'flexVCenter',
        MPColorClass.SolidNeutralGray5,
        CSSGap[24],
        styles.socialActionSection
      )}
    >
      <WalletAddress profileQueryRef={profileQueryRef} />

      {!!socialLinks.length && (
        <div className={joinClasses('flexVCenter', CSSGap[16])}>
          {!!profileData.openseaUrl && (
            <SocialIconLink
              socialMedia={{
                Icon: OpenSeaIcon,
                href: profileData.openseaUrl,
                linkType: 'opensea',
                tooltip: 'OpenSea',
              }}
            />
          )}
          {!!profileData.etherscanUrl && (
            <SocialIconLink
              socialMedia={{
                Icon: EtherscanIcon,
                href: profileData.etherscanUrl,
                linkType: 'etherscanUrl',
                tooltip: 'Etherscan',
              }}
            />
          )}
          {socialLinks.map((socialMedia) => (
            <SocialIconLink
              key={socialMedia.linkType}
              socialMedia={socialMedia}
            />
          ))}
        </div>
      )}
    </div>
  );
}
