import { useState } from 'react';

import {
  MPColorClass,
  MPFonts,
  MPMenu,
  MPMenuItem,
} from '@mp-frontend/core-components';
import {
  EtherscanIcon,
  MoreHorizontalIcon,
  OpenSeaIcon,
} from '@mp-frontend/core-components/icons';
import { joinClasses } from '@mp-frontend/core-utils';

import WalletAddress from 'pages/profiles/walletAddress';
import CSSIn from 'types/enums/css';
import CSSGlobal from 'types/enums/css/Global';

import useProfileData from '../../useProfileData';
import { HeaderProps } from '../types';

import * as styles from 'css/pages/profiles/ProfileHeader.module.css';

function MenuItem({ close, href, Icon, tooltipText }) {
  return (
    <MPMenuItem
      onClick={close}
      className={joinClasses(
        MPFonts.textSmallMedium,
        MPColorClass.SolidNeutralGray5
      )}
    >
      <a
        target="_blank"
        href={href}
        rel="noreferrer"
        className={CSSGlobal.DefaultLink}
      >
        <div
          className={joinClasses(CSSGlobal.Flex.RowCenterAlign, CSSIn.GAP[4])}
        >
          <Icon />
          <span>{tooltipText}</span>
        </div>
      </a>
    </MPMenuItem>
  );
}

export default function MoreActions({
  profileQueryRef,
  socialLinks,
}: Pick<HeaderProps, 'profileQueryRef' | 'socialLinks'>) {
  const profileData = useProfileData(profileQueryRef);
  const [socialMediaMenuEl, setSocialMediaMenuEl] = useState(null);
  const isSocialMediaMenuOpen = !!socialMediaMenuEl;
  const openSocialMediaMenu = (event) => {
    setSocialMediaMenuEl(event.currentTarget);
  };
  const closeSocialMediaMenu = () => {
    setSocialMediaMenuEl(null);
  };

  return !!profileData.address ||
    !!socialLinks.length ||
    !!profileData.openseaUrl ||
    !!profileData.etherscanUrl ? (
    <>
      <MoreHorizontalIcon
        className={styles.moreSocialMediaMenuIcon}
        onClick={openSocialMediaMenu}
      />
      <MPMenu
        anchorEl={socialMediaMenuEl}
        open={isSocialMediaMenuOpen}
        onClose={closeSocialMediaMenu}
      >
        {!!profileData.address && (
          <MPMenuItem
            onClick={closeSocialMediaMenu}
            key="copyWalletAddress"
            className={joinClasses(
              MPFonts.textSmallMedium,
              MPColorClass.SolidNeutralGray5
            )}
          >
            <WalletAddress profileQueryRef={profileQueryRef} />
          </MPMenuItem>
        )}
        {!!profileData.openseaUrl && (
          <MenuItem
            close={closeSocialMediaMenu}
            href={profileData.openseaUrl}
            Icon={OpenSeaIcon}
            key="openSea"
            tooltipText="OpenSea"
          />
        )}
        {!!profileData.etherscanUrl && (
          <MenuItem
            close={closeSocialMediaMenu}
            href={profileData.etherscanUrl}
            Icon={EtherscanIcon}
            key="etherscan"
            tooltipText="Etherscan"
          />
        )}
        {socialLinks.map((socialMedia) => (
          <MenuItem
            close={closeSocialMediaMenu}
            key={socialMedia.linkType}
            href={socialMedia.href}
            Icon={socialMedia.Icon}
            tooltipText={socialMedia.tooltip}
          />
        ))}
      </MPMenu>
    </>
  ) : (
    <></>
  );
}
